// ============================================
// Font Stack Sans Serif
// ============================================

$font-family-sans-serif:
  // Cross-platform generic font family (default user interface font)
  system-ui,
  // Safari for macOS and iOS (San Francisco)
  -apple-system,
  // Windows
  "Segoe UI",
  // Android
  Roboto,
  // Basic web fallback
  "Helvetica Neue", Arial,
  // Linux
  "Noto Sans",
  "Liberation Sans",
  // Sans serif fallback
  sans-serif,
  // Emoji fonts
  "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji" !default;

// ============================================
// Font Stack Monospace
// ============================================

$font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;


// ========================================================
// Base Spacing
// ========================================================

$base-padding:                          0.75rem; // 12 Pixels
$base-margin:                           0.75rem; // 12 pixels

// ============================================
// Base Font
// ============================================

$base-font: () !default;
$base-font: (
    'font-size':                        1rem,       // 16 pixels
);

$base-line: () !default;
$base-line: (
  'height':                             1.5,     // 24 pixels
  'height-sm':                          1.25,    // 20 pixels
  'height-lg':                          2,       // 32 pixels 
);

// ============================================
// Font Weight
// ============================================

$font-weight: () !default;
$font-weight: (

  'thin':                               100,
  'light':                              300,
  'normal':                             400,
  'bold':                               700,
  'ultra-bold':                         900,

);

// ============================================
// Font Size
// ============================================

$font-size-sm:                          map-get($map: $base-font, $key: 'font-size') * .875 !default;
$font-size-lg:                          map-get($map: $base-font, $key: 'font-size') * 1.25 !default;

// ============================================
// Line Size
// ============================================

$line-height-base:                      map-get($map: $base-line, $key: 'height');
$line-height-sm:                        map-get($map: $base-line, $key: 'height-sm');
$line-height-lg:                        map-get($map: $base-line, $key: 'height-lg');

// ============================================
// Heading Size
// ============================================

$heading-size: () !default;
$heading-size: (

    'h1':                               map-get($map: $base-font, $key: 'font-size') * 3,
    'h2':                               map-get($map: $base-font, $key: 'font-size') * 2.5,
    'h3':                               map-get($map: $base-font, $key: 'font-size') * 2,
    'h4':                               map-get($map: $base-font, $key: 'font-size') * 1.75,
    'h5':                               map-get($map: $base-font, $key: 'font-size') * 1.5,
    'h6':                               map-get($map: $base-font, $key: 'font-size') * 1.25,
    'p':                                map-get($map: $base-font, $key: 'font-size'),

);

// ============================================
// Base Root Elements
// ============================================

* {
    box-sizing: border-box;
    &::after,
    &::before {
        box-sizing: border-box;
    }
}
  
@at-root body {
    background-color: #fff;
    font-size: map-get($map: $base-font, $key: 'font-size');
    font-weight: map-get($map: $font-weight, $key: 'normal');
    line-height: $line-height-base;
    font-family: $font-family-sans-serif, $font-family-monospace;
    margin: 0;
    padding: 0;
}

// ============================================
// Font Sizes
// ============================================

/* Font Sizes */

.font-small {
    font-size: $font-size-sm;
}

.font-large {
    font-size: $font-size-lg
}

// ============================================
// Font Weight
// ============================================

/* Font Weight */

@each $key, $val in $font-weight {

    .weight-#{$key} {
        font-weight: $val;
    }

}

// ============================================
// Line Height
// ============================================

/* Line Height */

.line-base {
    line-height: $line-height-base;
}

.line-small {
    line-height: $line-height-sm;
}

.line-large {
    line-height: $line-height-lg;
}

// ============================================
// Heading Size
// ============================================

/* Heading Size */

@each $key, $val in $heading-size {

    #{$key} {
      font-size: #{$val};
      font-weight: map-get($map: $font-weight, $key: 'normal');
      line-height: map-get($map: $base-line, $key: 'height');
    }
}