// ============================================
// Base Web Colors
// ============================================

$colors: () !default;
$colors: (

  'black':                  #000000,
  'white':                  #ffffff,
  'silver':                 #c0c0c0,
  'gray':                   #808080,
  'red':                    #FF0000,
  'maroon':                 #800000,
  'yellow':                 #FFFF00,
  'olive':                  #808000,
  'lime':                   #00FF00,
  'green':                  #008000,
  'aqua':                   #00FFFF,
  'teal':                   #008080,
  'blue':                   #0000FF,
  'navy':                   #000080,
  'fuchsia':                #FF00FF,
  'purple':                 #800080,

);

// ========================================================
// Color Usage
// ========================================================

$theme-colors: () !default;
$theme-colors: (

    'primary':                         map-get($colors, 'blue'),
    'secondary':                       map-get($colors, 'gray'),
    'success':                         map-get($colors, 'green'),
    'danger':                          map-get($colors, 'red'),
    'warning':                         map-get($colors, 'yellow'),
    'info':                            map-get($colors, 'aqua'),
    'light':                           map-get($colors, 'white'),
    'dark':                            map-get($colors, 'black'),

);

@each $key, $val in $theme-colors {

  .bg-#{$key} {
    background-color: #{$val};
  }
  .text-#{$key} {
    color: $val !important;
  }
}