// ==================================================================
// Media Query Breakpoints
// ==================================================================

$breakpoints: () !default;
$breakpoints: (

    'small':                36rem, // 576 pixels
    'medium':               48rem, // 768 pixels 
    'large':                62rem, // 992 pixels
    'xlarge':               75rem, // 1,200 pixels

);

// ==================================================================
// Column Breakpoints
// ==================================================================

// We want to use a responsive grid-view with 12 columns, to have more control over the web page.
// First we must calculate the percentage for one column: 100% / 12 columns = 8.33%.

$grid-columns: () !default;
$grid-columns: (

    'col-1':                8.33%,
    'col-2':                16.66%,
    'col-3':                25%,
    'col-4':                33.33%,
    'col-5':                41.66%,
    'col-6':                50%,
    'col-7':                58.33%,
    'col-8':                66.66%,
    'col-9':                75%,
    'col-10':               83.33%,
    'col-11':               91.66%,
    'col-12':               100%

);

