    .grid-col-sm-auto {
        flex: 0 0 auto;
        width: auto;
        max-width: 100%;
    }

    @each $key, $val in $grid-small {

        .grid-#{$key} {
            flex: 0 0 $val !important;
            max-width: $val !important;
        }
    
    }

    @each $key, $val in $flex-order {

        .order-sm-#{$key} {
            order: $val;
        }
    }

    @each $key, $val in $offset-order {

        .offset-sm-#{$key} {
            order: $val;
